___CSS_LOADER_EXPORT___.push([module.id, "\n.duplicate-note-div[data-v-7cb697c0] {\n    margin-bottom: 10px;\n}\n.duplicate-text[data-v-7cb697c0] {\n    font-weight: bold;\n}\n.radio-button[data-v-7cb697c0] {\n    margin-top: 2px;\n    cursor: pointer;\n}\n.radio-text[data-v-7cb697c0] {\n    font: normal normal normal 16px/21px Roboto;\n    color: #212121;\n    margin-left: 6px;\n}\n.choose-existing-process-flow-dropdown[data-v-7cb697c0] {\n    width: 511px;\n    border: 1px solid #a1a1a1;\n    border-radius: 4px;\n    height: 40px;\n    padding: 8px;\n    font-size: 14px;\n    font-family: \"Roboto\";\n    margin-top: 3px;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/dialogs/create/createNewProcessWizard.vue"],"names":[],"mappings":";AAkMA;IACI,mBAAmB;AACvB;AAEA;IACI,iBAAiB;AACrB;AAEA;IACI,eAAe;IACf,eAAe;AACnB;AAEA;IACI,2CAA2C;IAC3C,cAAc;IACd,gBAAgB;AACpB;AAEA;IACI,YAAY;IACZ,yBAAyB;IACzB,kBAAkB;IAClB,YAAY;IACZ,YAAY;IACZ,eAAe;IACf,qBAAqB;IACrB,eAAe;AACnB","sourcesContent":["<script setup lang=\"ts\">\nimport { ref } from \"vue\";\nimport { container } from \"../../../../../domain/core/diContainer\";\nimport { ICreateModel, ICreateModelParam } from \"../../../../../domain/useCase/ICreateModel\";\nimport { IUseCaseExecutor } from \"../../../../../domain/useCase/IUseCaseExecutor\";\nimport { IViewModel } from \"../../../../../domain/viewModel/IViewModel\";\nimport { IStudio } from \"../../../../../domain/useCase/IStudio\";\nimport { IModel, IModule } from \"../../../../../domain/model/models\";\nimport { IProcessEditorUtil } from \"../../../../../domain/useCase/IProcessEditorUtil\";\nimport IDEInput from \"../../../components/application/IDEInput.vue\";\nimport { IIDEInputDefinitions } from \"../../../../../domain/presentation/IIDEInputDefinitions\";\nimport { CryptoHelper } from \"@stechquick/algae/lib/helpers/cryptoHelper\";\nimport RadioButton from \"../../radioButtonComp.vue\";\nimport CreateDialogFooter from \"./createDialogFooter.vue/createDialogFooter.vue\";\nimport { ICreateNewProcessWizardInline } from \"../../IComponentProps\";\n\n\ninterface IProcessFlow {\n    type?: \"create-new-process-flow\" | \"choose-existing-process-flow\";\n    newProcessFlowName?: string;\n    newProcessFlowNameRenderKey?: string;\n    selectedExistingBpmnFileID?: string;\n    selectedExistingBpmnFileName: string;\n    isNewProcessFlowNameValid: boolean;\n}\n\nconst param = defineProps<{ param: ICreateNewProcessWizardInline }>();\nconst props = param.param;\n\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst studio = container.resolve<IStudio>(IStudio);\nconst processEditorUtil = container.resolve<IProcessEditorUtil>(IProcessEditorUtil);\nconst owner = viewModel.studio.items.find((item) => item.ID == props.ownerId) as IModule;\nconst bpmnFiles = ref<Array<IModel>>(processEditorUtil\n    .getModelsOfModule(owner)\n    .bpmns);\n\nconst emit = defineEmits([\"close\"]);\n\nconst isNameValid = ref<boolean>(false);\n\nconst validations = ref<Record<\"name\", IIDEInputDefinitions[\"validations\"]>>({\n    name: { minAndMaxLength: { min: 2, max: 60 }, availableCharacters: true, nonSpaceCharacter: true }\n});\n\nconst name = ref(\"\");\n\nlet processFlowNameChanged = false;\n\nconst processFlow = ref({\n    selectedExistingBpmnFileName: \"\",\n    isNewProcessFlowNameValid: false,\n    newProcessFlowName: \"\",\n    newProcessFlowNameRenderKey: CryptoHelper.CreateGuid(),\n} as IProcessFlow);\n\nfunction create() {\n    executor.execute(\n        async () => {\n            const model = processEditorUtil.getEmptyProcessWizardModel(owner.name);\n            switch (processFlow.value.type) {\n                case \"create-new-process-flow\":\n                    model.usingExistingBpmnFile = false;\n                    model.processType = \"single_approval\";\n                    model.selectedExistingBpmnFileID = \"\";\n                    model.processFlowName = processFlow.value.newProcessFlowName || name.value;\n                    break;\n                case \"choose-existing-process-flow\":\n                    model.usingExistingBpmnFile = true;\n                    model.selectedExistingBpmnFileID = processFlow.value.selectedExistingBpmnFileID || \"\";\n                    break;\n                default:\n                    break;\n            }\n            const createObject: ICreateModelParam = {\n                name: name.value,\n                ownerId: props.ownerId,\n                modelType: \"process\",\n                modelBody: [{ key: \"process\", model }],\n                modelAdditionals: {},\n                path: props.path\n            };\n            if (props.duplicateFrom) {\n                createObject.duplicateFrom = props.duplicateFrom;\n            }\n            const createdModel = await executor.executeUseCase<ICreateModel>(ICreateModel, createObject);\n            await onAfterModelCreated(createdModel);\n            emit(\"close\");\n        },\n        { loading: true }\n    );\n}\n\nasync function onAfterModelCreated(createdModel: IModel) {\n    await studio.checkout(createdModel);\n    studio.openItem(createdModel);\n}\n\nfunction handleCreateNewProcessFlow(value: IProcessFlow[\"type\"]) {\n    processFlow.value.type = value;\n    switch (processFlow.value.type) {\n        case \"create-new-process-flow\":\n            processFlow.value.selectedExistingBpmnFileID = \"\";\n            processFlow.value.selectedExistingBpmnFileName = \"\";\n            processFlow.value.newProcessFlowNameRenderKey = CryptoHelper.CreateGuid();\n            setNewProcessFlowNameValue(name.value);\n            processFlowNameChanged = false;\n            break;\n        case \"choose-existing-process-flow\":\n            processFlow.value.isNewProcessFlowNameValid = false;\n            setNewProcessFlowNameValue(\"\");\n            break;\n        default:\n            break;\n    }\n}\n\nfunction handleSelectedExistingBpmnFileID($event: any) {\n    let selectedExistingBpmnFile = $event.target[$event.target.selectedIndex];\n    processFlow.value.selectedExistingBpmnFileID = selectedExistingBpmnFile.id;\n}\n\nfunction setNameValue(value: string) {\n    name.value = value;\n    if (!processFlowNameChanged) {\n        processFlow.value.newProcessFlowName = value;\n        processFlow.value.newProcessFlowNameRenderKey = CryptoHelper.CreateGuid();\n    }\n}\n\nfunction setNewProcessFlowNameValue(value: string) {\n    processFlow.value.newProcessFlowName = value;\n    processFlowNameChanged = true;\n}\n\nfunction setNameValidationStatus(isValid: boolean) {\n    isNameValid.value = isValid;\n}\n\nfunction setNewProcessFlowNameValidationStatus(isValid: boolean) {\n    processFlow.value.isNewProcessFlowNameValid = isValid;\n}\n</script>\n\n<template>\n    <div class=\"name-input-div\" style=\"padding-top: 32px;\">\n        <div v-if=\"props.duplicateFrom\" class=\"duplicate-note-div\">\n            <span class=\"label-value\">* This new Item will be duplicated from\n                <span class=\"duplicate-text\">{{ props.duplicateFrom.name }}.process </span>\n            </span>\n        </div>\n        <div class=\"input-label\">Process Name</div>\n        <IDEInput :isRequired=\"true\" elementId=\"nameInput\" :disable-required-icon=\"true\" @getValue=\"setNameValue\"\n            @validateStatus=\"setNameValidationStatus\" :placeholder=\"``\" :type=\"'text'\" info-text=\"\"\n            :validations=\"validations.name\" />\n        <div v-if=\"!props.duplicateFrom\">\n            <hr style=\"margin: 32px 0px 32px 0px\" />\n            <div style=\"display:flex;\">\n                <RadioButton class=\"radio-button\" :active=\"processFlow.type == 'create-new-process-flow'\"\n                    @click=\"handleCreateNewProcessFlow('create-new-process-flow')\" />\n                <span class=\"radio-text\"> Create New Process Flow (BPMN)</span>\n            </div>\n            <div v-if=\"processFlow.type == 'create-new-process-flow'\" style=\"margin-top: 16px;\">\n                <div class=\"input-label\">Process Flow Name</div>\n                <IDEInput :isRequired=\"true\" elementId=\"flowNameInput\" :disable-required-icon=\"true\" @getValue=\"setNewProcessFlowNameValue\"\n                    @validateStatus=\"setNewProcessFlowNameValidationStatus\"\n                    :key=\"processFlow.newProcessFlowNameRenderKey\" :value=\"processFlow.newProcessFlowName\"\n                    :placeholder=\"``\" :type=\"'text'\" info-text=\"\" :validations=\"validations.name\" />\n            </div>\n            <div style=\"display:flex; margin-top: 30px;\">\n                <RadioButton class=\"radio-button\" :active=\"processFlow.type == 'choose-existing-process-flow'\"\n                    @click=\"handleCreateNewProcessFlow('choose-existing-process-flow')\" />\n                <span class=\"radio-text\"> Choose Existing Process Flow (BPMN)</span>\n            </div>\n            <div v-if=\"processFlow.type == 'choose-existing-process-flow'\" style=\"margin-top: 16px;\">\n                <span class=\"input-label\"> BPMN File </span>\n                <select class=\"choose-existing-process-flow-dropdown\" name=\"choose-existing-process-flow\"\n                    id=\"choose-existing-process-flow\" v-model=\"processFlow.selectedExistingBpmnFileName\"\n                    @change=\"handleSelectedExistingBpmnFileID\" required>\n                    <option value=\"\" disabled selected>Choose Existing Process Flow (BPMN)</option>\n                    <option v-for=\"bpmnFile in bpmnFiles\" :id=\"bpmnFile.ID\">\n                        {{ bpmnFile.name }}\n                    </option>\n                </select>\n            </div>\n        </div>\n    </div>\n    <CreateDialogFooter :cancel-button-cb=\"() => emit('close')\" :create-button-cb=\"create\"\n        :create-button-enabled=\"isNameValid && (!!processFlow.selectedExistingBpmnFileName.length || processFlow.isNewProcessFlowNameValid)\" />\n</template>\n\n<style scoped>\n@import url(\"../../../../../styles/IDE3.css\");\n.duplicate-note-div {\n    margin-bottom: 10px;\n}\n\n.duplicate-text {\n    font-weight: bold;\n}\n\n.radio-button {\n    margin-top: 2px;\n    cursor: pointer;\n}\n\n.radio-text {\n    font: normal normal normal 16px/21px Roboto;\n    color: #212121;\n    margin-left: 6px;\n}\n\n.choose-existing-process-flow-dropdown {\n    width: 511px;\n    border: 1px solid #a1a1a1;\n    border-radius: 4px;\n    height: 40px;\n    padding: 8px;\n    font-size: 14px;\n    font-family: \"Roboto\";\n    margin-top: 3px;\n}\n</style>\n"],"sourceRoot":""}]);
